<template>
  <tas-base-crud ref="sub-module" :config="config" hide-update hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="openModal('modal-rincian-bills', data)" class="btn btn-info btn-sm">Detail Rincian </a>
    </template>
    <template v-slot:list-header>
      <b-modal header-class="pb-1" v-if="modalData" id="modal-rincian-bills" hide-footer size="xl">
        <template #modal-header>
          <div class="d-flex justify-content-between flex-fill">
            <div class="d-flex flex-column flex-fill">
              <div class="text-center mb-5">
                <h5 class="mb-1 font-weight-bold">Detail Rincian Iuran BPJS Tenaga Kerja</h5>
                <span class="font-weight-bolder">Periode {{ modalData.detail.periode_month | parse('period') }}</span>
              </div>
              <table class="table no-border mb-0">
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Tenaga Kerja</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.rel_employee_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Tanggal Lahir</td>
                  <th class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.date_of_birth | parse('longDate') }} </th>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Jabatan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_job_position_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Umur</td>
                  <th class="pt-0 pr-0 nowrap-table">: {{ getAge(modalData.detail.date_of_birth, modalData.detail.periode_month) }} Tahun </th>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Status Ketenagakerjaan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_status_employment_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Nomor KPJ</td>
                  <th class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.nomor_kpj_bpjs_tk || '-' }} </th>
                </tr>
              </table>
            </div>
          </div>
        </template>
        <div class="row">
          <div class="col-12" v-if="modalData.detail">
            <h5 class="mb-4">Detail Rincian Iuran BPJS Tenaga Kerja</h5>
            <table class="table table-custom-header v-center table-bordered">
              <tbody>
                <tr>
                  <td colspan="4" class="font-weight-bolder bg-light-primary text-uppercase">Upah Tenaga Kerja</td>
                </tr>
                <tr>
                  <th>Nama Komponen</th>
                  <th class="nowrap-table">Koefisien</th>
                  <th class="text-center">Nilai Dasar</th>
                  <th class="text-center">Nilai Komponen</th>
                </tr>
                <tr v-for="(k,i) in modalData.detail.child_data_bpjs_tk_bill_detail_upah_components" :key="i+'-komponen-iuran'">
                  <td>{{ k.rel_salary_component_id }}</td>
                  <td class="bg-light-primary">
                    <s-input v-model="k.koefisien" :item="{ type:'number', validation:[], label:null, prefix: null, groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:0 } }" />
                  </td>
                  <td class="bg-light-primary">
                    <s-input v-model="k.default_value" :item="{ type:'number', validation:[], label:null, prefix: 'Rp. ', groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:2 } }" />
                  </td>
                  <td>
                    <div class="d-flex justify-content-between">
                      <span class="">Rp.</span>
                      <span class="text-right font-weight-bold">{{ (k.koefisien * k.default_value) || 0 | parse('number') }}</span>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="row">
              <div class="col-6">
                <div class="py-3 px-5 border-primary" style="border:2px solid;border-radius:8px">
                  <ul class="list-unstyled mb-0">
                    <li class="mb-3">
                      <s-input v-model="modalData.detail.kekurangan_jp_value" :item="{ type:'number', validation:[], label:'Kekurangan Iuran JP', prefix: 'Rp. ', groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:2 } }" />
                    </li>
                    <li class="mb-3">
                      <label class="checkbox checkbox-md">
                        <input type="checkbox" v-model="modalData.detail.is_beban_company" name="bpjs_perusahaan">
                        <span class="mr-3"></span>Checklist apabila BPJS ditanggung Perusahaan</label>
                    </li>
                    <li class="mb-0">Iuran BPJS Beban Perusahaan &emsp;&nbsp;&nbsp;: Rp. {{ modalData.detail.iuran_bpjs_company_value | parse('number') }}</li>
                    <li>Iuran BPJS Beban Tenaga Kerja &emsp;: Rp. {{ modalData.detail.iuran_bpjs_employee_value | parse('number') }}</li>
                  </ul>
                </div>
              </div>
              <div class="col-6">
                <div class="form-group mb-5 mt-0">
                  <label>Deskripsi Rincian Iuran BPJS Tenaga Kerja</label>
                  <textarea v-model="modalData.detail.description" class="form-control" rows="6" placeholder="Deskripsi / note Rincian iuran simpan pinjam"></textarea>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-5">
                <div class="table-responsive">
                  <table class="table no-border v-center">
                    <tr>
                      <td class="nowrap-table py-1 pl-0">Upah Tenaga Kerja</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail.upah_value || 0 | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-0">Iuran JKK</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail.iuran_jkk_value || 0 | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-0">Iuran JKM</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail.iuran_jkm_value || 0 | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-0">Iuran JP Perusahaan</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail.iuran_jp_bb_company_value || 0 | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-0">Iuran JP Tenaga Kerja</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail.iuran_jp_bb_employee_value || 0 | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-0">Iuran JHT Perusahaan</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail.iuran_jht_company_value || 0 | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-0">Iuran JHT Tenaga Kerja</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1">
                        <div class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right font-weight-bold">{{ modalData.detail.iuran_jht_employee_value || 0 | parse('number') }}</span>
                        </div>
                      </td>
                    </tr>
                  </table>
                </div>
              </div>
              <div class="col-5 offset-2">
                <div class="bg-light-primary p-3" style="border-radius:8px">
                  <h6 class="mb-4">Informasi Premi</h6>
                  <div class="table-responsive mb-0">
                    <table v-if="modalData.detail.data_premi" class="table no-border v-center">
                      <tr>
                        <td class="nowrap-table py-0 pl-0">Usia Maksimal</td>
                        <td class="nowrap-table py-0">:</td>
                        <td class="py-0 text-right"> {{ modalData.detail.data_premi.max_age || 0 | parse('number') }} Tahun </td>
                      </tr>
                      <tr>
                        <td class="nowrap-table py-0 pl-0">Upah Maksimal (JP)</td>
                        <td class="nowrap-table py-0">:</td>
                        <td class=" py-0">
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ modalData.detail.data_premi.upah_max_jp_value || 0 | parse('number') }}</span>
                          </div>
                        </td>
                      </tr>
                      <tr>
                        <td class="nowrap-table py-0 pl-0">Persentase JKK</td>
                        <td class="nowrap-table py-0">:</td>
                        <td class="py-0 text-right"> {{ modalData.detail.data_premi.prosen_jkk || 0 | parse('number') }} % </td>
                      </tr>
                      <tr>
                        <td class="nowrap-table py-0 pl-0">Persentase JP Perusahaan</td>
                        <td class="nowrap-table py-0">:</td>
                        <td class="py-0 text-right"> {{ modalData.detail.data_premi.prosen_jp_bb_company || 0 | parse('number') }} % </td>
                      </tr>
                      <tr>
                        <td class="nowrap-table py-0 pl-0">Persentase JP Tenaga Kerja</td>
                        <td class="nowrap-table py-0">:</td>
                        <td class="py-0 text-right"> {{ modalData.detail.data_premi.prosen_jp_bb_employee || 0 | parse('number') }} % </td>
                      </tr>
                      <tr>
                        <td class="nowrap-table py-0 pl-0">Persentase JHT Perusahaan</td>
                        <td class="nowrap-table py-0">:</td>
                        <td class="py-0 text-right"> {{ modalData.detail.data_premi.prosen_jht_company || 0 | parse('number') }} % </td>
                      </tr>
                      <tr>
                        <td class="nowrap-table py-0 pl-0">Persentase JHT Tenaga Kerja</td>
                        <td class="nowrap-table py-0">:</td>
                        <td class="py-0 text-right"> {{ modalData.detail.data_premi.prosen_jht_employee || 0 | parse('number') }} % </td>
                      </tr>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="mt-5">
          <button @click="submitData('modal-rincian-bills')" class="btn btn-primary btn-lg btn-block font-size-md">Perbarui &amp; Simpan</button>
        </div>
      </b-modal>
    </template>
  </tas-base-crud>
</template>

<script>

export default {
  name: 'crud-bpjs-tk-bill-details',
  data () {
    return {
      mainKey: 1,
      modalData: {},
      config: {
        title: 'Rincian Iuran BPJS Tenaga Kerja',
        model_api: null,
        getter: 'bpjs-tk-bill-details',
        setter: 'bpjs-tk-bill-details',
        pk_field: 'employee_id',
        permission: {
          create: 'create-bpjs-tk-bill-details',
          read: 'view-bpjs-tk-bill-details',
          update: 'update-bpjs-tk-bill-details',
          delete: 'delete-bpjs-tk-bill-details'
        },
        custom_api: {
          list: 'custom/bpjs-tk-bill-details/list',
          create: 'custom/bpjs-tk-bill-details'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'bpjs_tk_bill_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'koperasi_simpan_pinjam_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'rel_job_position_id', label: 'Jabatan', methods: { list: true, detail: false, create: false, update: false, filter: false } },
          { id: 'rel_status_employment_id', label: 'Status Ketenagakerjaan', methods: { list: true, detail: false, create: false, update: false, filter: false } },
          {
            id: 'status_code',
            data: 'status_code',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              detail: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              create: false,
              update: {
                view_data: 'status_code',
                type: 'radio',
                value: 'calculated',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              },
              filter: {
                view_data: 'status_code',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              }
            }
          },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'upah_value', label: 'Upah Tenaga Kerja', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_jkk_value', label: 'Iuran JKK', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_jkm_value', label: 'Iuran JKM', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_jp_bb_company_value', label: 'Iuran Perusahaan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_jp_bb_employee_value', label: 'Iuran Tenaga Kerja', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_jht_company_value', label: 'Iuran JHT Perusahaan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_jht_employee_value', label: 'Iuran JHT Tenaga Kerja', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'kekurangan_jp_value', label: 'Kekurangan Iuran JP', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_bpjs_company_value', label: 'Iuran Beban Perusahaan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_bpjs_employee_value', label: 'Iuran Beban Tenaga Kerja', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'total_iuran_value', label: 'Total', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'is_beban_company',
            data: 'is_beban_company',
            label: 'Beban Perusahaan',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              detail: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              create: false,
              update: false,
              filter: {
                view_data: 'is_beban_company',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  },
  watch: {
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        // console.log(this.$refs['sub-module'])
        this.$refs['sub-module'].$children[2].getData()
      }
    }
  },
  methods: {
    async openModal (id, data) {
      const detail = {
        detail: await this.getDetail('custom/bpjs-tk-bill-details/show', { id: data.rowData.id })
      }
      this.modalData = Object.assign({}, detail, data.rowData)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    getDetail (api, param, obj = null) {
      return this.$_api.get(api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    getAge (dateString, period) {
      var today = new Date(period)
      var birthDate = new Date(dateString)
      var age = today.getFullYear() - birthDate.getFullYear()
      var m = today.getMonth() - birthDate.getMonth()
      if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
        age--
      }
      return age
    },
    submitData (modalID) {
      const api = 'custom/bpjs-tk-bill-details/calculate'
      this.$_api.post(api, this.modalData.detail).then(res => {
        this.mainKey++
        this.$_alert.success(null, 'Detail Rincian berhasil diperbarui')
        this.$set(this.modalData, 'detail', res.data)
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>
<style scoped>
  .in-card-border {
    border: 2px solid;
    border-radius: 8px;
    padding: 1rem;
  }
</style>



